@media screen and (max-width:814px){
    .card-container {
        width: 310px;
        gap: 21px;
        padding: 0;
        padding-bottom: 21px;
        flex-direction: column;
        align-items: center;
        justify-content: start;

    }

    .card-text-container {
        min-width: 248px;
    }
    
    .card-text-container h3{
        text-align: center;
        height: fit-content;
    }

   
    .stats-container {
        display: none;
    }

    .plant-image-container {
        order: -1;
        max-width: 310px;
        max-height: none;
        aspect-ratio: 310/225;
        border-radius: 6px 6px 0px 0px;
    }

    .plant-image {
        overflow: hidden;
        height: 225px;
        border-radius: 6px 6px 0px 0px;


    }
    .beskrivelse-container {
        height: fit-content;

    }

    .beskrivelse-container p {
        white-space: nowrap;
        width: 248px;
        text-align: center;
        overflow: hidden ;
        text-overflow: ellipsis;
    }

    .mobile-card-button {
        display: inline-block;
        color: white;
        align-items: center;
        font-family: Raleway;
        font-size: 19px;
        font-weight: 700;
        background-color: #2E4D42;
        border: 1px solid #2E4D42;
        border-radius: 6px;
        height: 44px;
        padding: 0 24px ;
        cursor: pointer;
    }

    .mobile-card-button:hover {
        font-weight: 700;
        background-color: #46724F ;
        color: white;
        box-shadow: 0 1px 1px rgba(0, 0, 0, 0.304);
        border: 1px solid black;
    }
}